.category {
  margin-bottom: 70px;
}

.infos {
  /* border: solid 1px red; */
}

.picture {
  width: 47%;
  min-width: 250px;
  float: right;
  height: 100px;
  /* background-blend-mode: multiply; */
  /* border: solid 1px red; */
  /* border: solid 1px black; */
}

.picture .categorypicture {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.categoryName {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 25px;
  font-size: 25px;
  height: 40px;
  /* border: solid 1px red; */
}

.food {
  display: block;
  width: 47%;
  cursor: pointer;
  transition: all 10s ease-in-out 5s;
}

/* .food.selected {
  background-color: green;
  position: relative;
  z-index: 99;
} */

.food.selected {
  /* position: fixed; */
  top: 0;
  bottom: 0;
  /* margin: auto; */
  height: fit-content;
  /* width: 28%; */
  z-index: 35;
  background-color: #f39f86;
  background-image: linear-gradient(315deg, #f39f86 0%, #f9d976 74%);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.foodWrapper.selected {
  /* padding: 0 2vw; */
}

.food:nth-child(1) {
  border-top: solid 1px #808080;
  border-bottom: solid 1px #808080;
  padding: 10px 0;
}

.food:not(:first-child) {
  border-bottom: solid 1px #808080;
  padding: 10px 0;
}

.foodName,
.foodPrice {
  font-weight: bold;
  text-transform: capitalize;
}

.foodPrice::after {
  content: "FCFA";
  padding-left: 3px;
  /* border: solid 1px green; */
}

.foodDescription {
  max-width: 90%;
  color: #808080;
}

@media screen and (max-width: 1000px) {
  .food.selected {
    width: 40%;
  }
}

@media screen and (max-width: 800px) {
  .food {
    width: 100%;
  }
  .food.selected {
    width: 90%;
    left: 5vw;
    margin: 0;
    top: 8%;
  }
  .picture {
    /* background-color: blue; */
    float: none;
    margin-bottom: 20px;
  }
}
